const [activeStep, setActiveStep] = [1, 2]
const steps = getSteps();
const handleReset = () => {
    setActiveStep(0);
};

class Form extends Component {

    sendFormData = async () => {
        try {
            const { data } = await axios.post('formSaveToDatabase', { data: this.state })
            return data;
        } catch (err) {
            console.log(err.message);
        }
    }

    shouldComponentUpdate(old, newP) {
        console.log("should update");
        return true;
    }

    inputChangedHandler = (event, inputKey, type, page, element) => {
        // Save the data
        console.log(type);
        console.log(page);
        console.log(inputKey);

        console.log("Next!!!!");
        console.log(event);
        const updatedForm = this.state
        const updatedFormElement = { ...this.state[inputKey] }

        switch (type) {
            case ('button-next'):
                // Load next page
                console.log(this.state)
                console.log(parseInt(page) + 1)
                let nextPage = parseInt(page) + 1
                this.props.history.push('/form/' + nextPage, { state: this.state })
                // nextPage == 4 ? this.props.history.push('/form/' + nextPage, { state: this.state }) : this.schedule()
                break;
            case ('button-submit'):
                // https://www.pluralsight.com/guides/all-need-to-know-about-axios
                axios.post('formSaveToDatabase', { data: this.state }).then(response => this.redirect())
                    .catch(err => {
                        console.log(err.message);
                    })
                break;
            case ('cards-container'):
                // Save the values
                // console.log(updatedFormElement)
                if (updatedForm.selected.includes(event.text)) {
                    updatedForm.selected = updatedForm.selected.filter(item => item !== event.text)
                } else {
                    updatedForm.selected = updatedForm.selected.concat(event.text)
                }
                console.log(event.text)
                this.setState(updatedForm)
                break;
            case ('time-slots'):
                // Save the values
                // console.log(updatedFormElement)
                if (updatedForm.selected.includes(event)) {
                    updatedForm.selected = updatedForm.selected.filter(item => item !== event)
                } else {
                    updatedForm.selected = updatedForm.selected.concat(event)
                }
                console.log(event)
                this.setState(updatedForm)
                break;
            default:
                // Save the values
                console.log(updatedFormElement)
                updatedFormElement.value = event.target.value
                updatedForm[inputKey] = updatedFormElement
                console.log(updatedFormElement)
                this.setState(updatedForm)
                break;

        }
    }

    redirect = () => {
        this.props.history.push({
            pathname: '/success'
        })
    }

    schedule = () => {
        this.props.history.push({
            pathname: '/schedule'
        })
    }

    componentDidMount() {
        console.log("componentDidMount")
        const { id } = this.props.match.params
        console.log(id)
        let formArray = [];

        let page = parseInt(id) || 0
        // console.log(formData)

        const pageInfo = formData[page];
        let index = 0
        let state = this.state ?? {}
        pageInfo.stepInputs.map(element => {
            let id = index + page * 10;
            let value = ""
            if (this.state != null && this.state[id] != null) {
                value = this.state[id].value ?? ""
            }
            let elementInfo = {
                id: id,
                info: element,
                selected: this.state != null ? this.state.selected : [],
                value: value
            }
            state[id] = elementInfo
            formArray.push(elementInfo)
            index += 1
        })

        this.setState({
            state,
            redirect: false,
            selected: []
        })
        console.log(state)
        console.log("componentDidMount")
    }

    render() {
        console.log("render")
        if (this.state != null && this.state.redirect) {
            console.log("Redirect")
            this.redirect()
            return
        }
        const { id } = this.props.match.params
        console.log(id)
        let formArray = [];

        let page = parseInt(id) || 0
        // console.log(formData)

        const pageInfo = formData[page];
        const title = pageInfo.stepTitle;
        const subtitle = pageInfo.stepSubtitle;

        let index = 0
        let state = this.state ?? {}
        pageInfo.stepInputs.map(element => {
            let id = index + page * 10;
            let value = ""
            if (this.state != null && this.state[id] != null) {
                value = this.state[id].value ?? ""
            }
            let elementInfo = {
                id: id,
                info: element,
                selected: this.state != null ? this.state.selected : [],
                value: value
            }
            state[id] = elementInfo
            formArray.push(elementInfo)
            index += 1
        })

        return (
            <Page>
                <Section>
                    <Row>
                        <Stepper alternativeLabel>
                            {steps.map(label => (
                                <Step key={label}>
                                    <StepLabel>{label}</StepLabel>
                                </Step>
                            ))}
                        </Stepper>
                        <div>
                            {activeStep === steps.length ? (
                                <div>

                                    <Button onClick={handleReset}>Reset</Button>
                                </div>
                            ) : (
                                    <div>

                                        <div>

                                        </div>
                                    </div>
                                )}
                        </div>
                    </Row>
                    <Row classes="largeTitle">
                        <h2>
                            <mark><FormattedHTMLMessage id={title}></FormattedHTMLMessage></mark>
                        </h2>
                        <p className="subtitle"><FormattedHTMLMessage id={subtitle}></FormattedHTMLMessage></p>
                    </Row>

                    <Row >
                        <form onSubmit={this.onSubmit}>
                            {formArray.map(element => (
                                <Input
                                    changed={(event) => this.inputChangedHandler(event, element.id, element.info.type, page, element)}
                                    elementType={element.info.type}
                                    elementConfig={element.info.config}
                                    label={element.info.name}
                                    key={element.id}
                                    selected={element.selected} />
                            ))}
                        </form >

                    </Row>
                </Section>
            </Page>
        );
    }
}

const mapStateToProps = state => {
    return {
        //name: state.value -> to tuse this.props.name
    };
}

const mapDispatchToProps = dispatch => {
    return {
        // name () => { dispatch({type: 'NAME', value: 10, otherStuff: , payload: {}})} -> to user this.props.name
        // to user in reducer action.value or action.payload
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Form);